import React, { useState } from "react";
import axios from "axios";

import "./rsvp.scss";

const RSVP = () => {
  const [rsvpState, setRsvpState] = useState({
    answer: "",
    firstName: "",
    lastName: "",
    food: "",
    drink: [],
    guestFirstName: "",
    guestLastName: "",
    guestFood: "",
    guestDrink: [],
    message: "",
  });
  const [contentState, setContentState] = useState("form");
  const [guest1Display, setGuest1Display] = useState(false)
  const [guest2Display, setGuest2Display] = useState(false)

  const formSubmitted = (event) => {
    event.preventDefault();

    axios
      .post(
        "https://my-wedding-website-701bb-default-rtdb.firebaseio.com/rsvp.json",
        rsvpState
      )
      .then((response) => {
        console.log(response);
        setContentState("success");
      })
      .catch((error) => {
        console.log(error);
        setContentState("error");
      });
  };

  const inputChangedHandler = (event) => {
    const input = event.target.id;

    switch (input) {
      case "rsvp-yes":
        setRsvpState({
          ...rsvpState,
          answer: event.target.value,
        });
        setGuest1Display(true)
        break;
      case "rsvp-no":
        setRsvpState({
          ...rsvpState,
          answer: event.target.value,
        });
        setGuest1Display(false)
        setGuest2Display(false)
        break;
      case "firstName":
        setRsvpState({
          ...rsvpState,
          firstName: event.target.value,
        });
        break;
      case "lastName":
        setRsvpState({
          ...rsvpState,
          lastName: event.target.value,
        });
        break;
      case "veggie":
        setRsvpState({
          ...rsvpState,
          food: event.target.value,
        });
        break;
      case "chicken":
        setRsvpState({
          ...rsvpState,
          food: event.target.value,
        });
        break;
      case "guest2firstName":
        setRsvpState({
          ...rsvpState,
          guestFirstName: event.target.value,
        });
        break;
      case "guest2lastName":
        setRsvpState({
          ...rsvpState,
          guestLastName: event.target.value,
        });
        break;
      case "guest2chicken":
        setRsvpState({
          ...rsvpState,
          guestFood: event.target.value,
        });
        break;
      case "guest2veggie":
        setRsvpState({
          ...rsvpState,
          guestFood: event.target.value,
        });
        break;
      case "message":
        setRsvpState({
          ...rsvpState,
          message: event.target.value,
        });
        break;
      default:
        console.log("no rsvp case met")
    }
  };

  const inputChangedGuest2Handler = (event) => {
    const input = event.target.id;

    if (input === "guest2-yes") {
      setGuest2Display(true)
    } else if (input === 'guest2-no') {
      setGuest2Display(false)
    }
  };

  const drinkSelectionHandler = (event) => {
    let checkbox = event.target;

    const guest1Drinks = rsvpState.drink;
    const guest2Drinks = rsvpState.guestDrink;

    console.log(guest1Drinks, guest2Drinks)

    if (checkbox.name === 'drink') {
      if (checkbox.checked) {
        guest1Drinks.push(checkbox.value)
      } else {
        guest1Drinks.forEach((drink, index) => {
          if (drink === checkbox.value) {
            guest1Drinks.splice(index,1)
          }
        })
      }
      console.log(guest1Drinks)
      setRsvpState({
        ...rsvpState,
        drink: guest1Drinks,
      });

    } else if (checkbox.name === 'drink2') {
      if (checkbox.checked) {
        guest2Drinks.push(checkbox.value)
      } else {
        guest2Drinks.forEach((drink, index) => {
          if (drink === checkbox.value) {
            guest2Drinks.splice(index,1)
          }
        })
      }
      console.log(guest2Drinks)
      setRsvpState({
        ...rsvpState,
        guestDrink: guest2Drinks,
      });
    }
  }

  let formContent = (
    <form className="rsvp-form" onSubmit={formSubmitted}>
      <section className="rsvp-form__radio" id="rsvpSection">
        <h2 className="rsvp-form__label--rsvpQuestion">Will you be attending?</h2>
        <label className="rsvp-form__radio-label" htmlFor="rsvp-yes">
          I Gladly Accept
        </label>
        <input
          className="rsvp-form__radio-input"
          type="radio"
          id="rsvp-yes"
          name="rsvp"
          value="yes"
          onChange={inputChangedHandler}
        />
        <label className="rsvp-form__radio-label" htmlFor="rsvp-no">
          I Regretfully Decline
        </label>
        <input
          className="rsvp-form__radio-input"
          type="radio"
          id="rsvp-no"
          name="rsvp"
          value="no"
          onChange={inputChangedHandler}
        />
      </section>
      <hr className={guest1Display ? '' : 'hide'}/>
      <section className={guest1Display ? '' : 'hide'}>
        <h2 className="rsvp-form__sectionHeader">Guest 1</h2>
        <div>
          <label className="rsvp-form__label" htmlFor="firstName">
            First Name:
          </label>
          <input
            className="rsvp-form__input"
            type="text"
            id="firstName"
            placeholder="First Name"
            onChange={inputChangedHandler}
          />
        </div>
        <div>
          <label className="rsvp-form__label" htmlFor="lastName">
            Last Name:
          </label>
          <input
            className="rsvp-form__input"
            type="text"
            id="lastName"
            placeholder="Last Name"
            onChange={inputChangedHandler}
          />
        </div>
        <div>
          <p className="rsvp-form__label rsvp-form__label--question">What would you like for dinner?</p>
          <label className="rsvp-form__radio-label" htmlFor="chicken">
            Chicken
          </label>
          <input
            className="rsvp-form__radio-input"
            type="radio"
            id="chicken"
            name="food1"
            value="chicken"
            onChange={inputChangedHandler}
          />
          <label className="rsvp-form__radio-label" htmlFor="rsvp-no">
            Vegetarian
          </label>
          <input
            className="rsvp-form__radio-input"
            type="radio"
            id="veggie"
            name="food1"
            value="veggie"
            onChange={inputChangedHandler}
          />
        </div>
        <div>
          <p className="rsvp-form__label rsvp-form__label--question">What do you like to drink? (Check all that apply)</p>
          <div className="rsvp-form__checkbox">
            <input className="rsvp-form__checkbox-input" type="checkbox" id="soda" name="drink" value="soda" onChange={drinkSelectionHandler}/>
            <label className="rsvp-form__checkbox-label" htmlFor="soda"> Soda/Pop</label>
            <input className="rsvp-form__checkbox-input"  type="checkbox" id="beer" name="drink" value="beer" onChange={drinkSelectionHandler}/>
            <label className="rsvp-form__checkbox-label" htmlFor="beer"> Beer</label>
            <input className="rsvp-form__checkbox-input"  type="checkbox" id="cider" name="drink" value="cider" onChange={drinkSelectionHandler}/>
            <label className="rsvp-form__checkbox-label" htmlFor="cider"> Cider</label>
            <input className="rsvp-form__checkbox-input"  type="checkbox" id="wine" name="drink" value="wine" onChange={drinkSelectionHandler}/>
            <label className="rsvp-form__checkbox-label" htmlFor="wine"> Wine</label>
            <input className="rsvp-form__checkbox-input"  type="checkbox" id="champagne" name="drink" value="champagne" onChange={drinkSelectionHandler}/>
            <label className="rsvp-form__checkbox-label" htmlFor="champagne"> Champagne / Sparkling Wine</label>
          </div>
        </div>
      </section>
      <section className={guest1Display ? '' : 'hide'}>
        <p className="rsvp-form__label rsvp-form__label--question">Are you RSVP-ing another guest?</p>
        <label className="rsvp-form__radio-label" htmlFor="guest2-yes">
          Yes
        </label>
        <input
          className="rsvp-form__radio-input"
          type="radio"
          id="guest2-yes"
          name="guest2"
          value="yes"
          onChange={inputChangedGuest2Handler}
        />
        <label className="rsvp-form__radio-label" htmlFor="guest2-no">
          No
        </label>
        <input
          className="rsvp-form__radio-input"
          type="radio"
          id="guest2-no"
          name="guest2"
          value="no"
          onChange={inputChangedGuest2Handler}
        />
      </section>
      <section className={guest2Display ? '' : 'hide'}>
        <hr />
        <h2 className="rsvp-form__sectionHeader">Guest 2</h2>
        <div>
          <label className="rsvp-form__label" htmlFor="guest2firstName">
            First Name:
          </label>
          <input
            className="rsvp-form__input"
            type="text"
            id="guest2firstName"
            placeholder="First Name"
            onChange={inputChangedHandler}
          />
        </div>
        <div>
          <label className="rsvp-form__label" htmlFor="guest2lastName">
            Last Name:
          </label>
          <input
            className="rsvp-form__input"
            type="text"
            id="guest2lastName"
            placeholder="Last Name"
            onChange={inputChangedHandler}
          />
        </div>
        <div>
          <p className="rsvp-form__label rsvp-form__label--question">What would you like for dinner?</p>
          <label className="rsvp-form__radio-label" htmlFor="guest2chicken">
            Chicken
          </label>
          <input
            className="rsvp-form__radio-input"
            type="radio"
            id="guest2chicken"
            name="food2"
            value="chicken"
            onChange={inputChangedHandler}
          />
          <label className="rsvp-form__radio-label" htmlFor="guest2veggie">
            Vegetarian
          </label>
          <input
            className="rsvp-form__radio-input"
            type="radio"
            id="guest2veggie"
            name="food2"
            value="veggie"
            onChange={inputChangedHandler}
          />
        </div>
        <div>
          <p className="rsvp-form__label rsvp-form__label--question">What do you like to drink? (Check all that apply)</p>
          <div className="rsvp-form__checkbox">
            <input className="rsvp-form__checkbox-input" type="checkbox" id="soda2" name="drink2" value="soda" onChange={drinkSelectionHandler}/>
            <label className="rsvp-form__checkbox-label" htmlFor="soda2"> Soda/Pop</label>
            <input className="rsvp-form__checkbox-input" type="checkbox" id="beer2" name="drink2" value="beer" onChange={drinkSelectionHandler}/>
            <label className="rsvp-form__checkbox-label" htmlFor="beer2"> Beer</label>
            <input className="rsvp-form__checkbox-input" type="checkbox" id="cider2" name="drink2" value="cider" onChange={drinkSelectionHandler}/>
            <label className="rsvp-form__checkbox-label" htmlFor="cider2"> Cider</label>
            <input className="rsvp-form__checkbox-input" type="checkbox" id="wine2" name="drink2" value="wine" onChange={drinkSelectionHandler}/>
            <label className="rsvp-form__checkbox-label" htmlFor="wine2"> Wine</label>
            <input className="rsvp-form__checkbox-input" type="checkbox" id="champagne2" name="drink2" value="champagne" onChange={drinkSelectionHandler}/>
            <label className="rsvp-form__checkbox-label" htmlFor="champagne2"> Champagne / Sparkling Wine</label>
          </div>
        </div>
      </section>
      <hr />
      <section>
        <label htmlFor="message" className="rsvp-form__label rsvp-form__label--question">
          Would you like to leave a message for the couple?
        </label>
        <textarea id="message" onChange={inputChangedHandler} />
      </section>
      <button className="rsvp-form__btn" type="submit">
        Submit Form
      </button>
    </form>
  );

  let successContent = (
    <p className="success-message">Form Submitted Successfully</p>
  );

  let errorContent = <p className="error-message">An error occured</p>;

  let mainContent = "";

  if (contentState === "form") {
    mainContent = formContent;
  } else if (contentState === "success") {
    mainContent = successContent;
  } else if (contentState === "error") {
    mainContent = errorContent;
  }

  return (
    <div>
      <h1>RSVP</h1>
      {mainContent}
    </div>
  );
};

export default RSVP;
